@import "_var";


.max-200 {
    max-width: 200px;
    width: 100%;
}

.w-nav {
    position: fixed;
    @include flex-center;
    width: 100%;
    height: auto;
    z-index: 100;
    padding: 20px 0;
    transition: all 200ms ease-in-out;
    border-bottom: 1px solid rgba(239, 239, 242, 0.2);
    background-color: transparent;
}

.w-nav-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    min-height: 100%;
    height: 100%;
    transition: all .3s ease;
}

.nav-logo {
    @include flex-center;
    justify-content: flex-start;

    img {
        max-height: 64px;
        height: 32px;
    }
}

.nav-links {
    max-width: 500px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    gap: 60px;
}

.nav-items {
    @include urbanist-font(500, 16px, $off-black);
}

.nav-items {
    color: $white;
    transition: all .3s ease;

    &:hover {
        color: $primary;
    }
}

.nav-cta {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: fit-content;
}

.menu-icon {
    max-width: 42px;
    max-height: 42px;
    min-width: 42px;
    min-height: 42px;
    width: 100%;
    height: 100%;
    padding: 2px;
    background-color: $primary;
    border-radius: 8px;
    cursor: pointer;
}

.menu-icon.active {
    background-color: $white;
    padding: 4px
}

.nav-icon {
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.squeeze {
    animation: squeeze 300ms forwards; 
}

@keyframes squeeze {
    0% {
        transform: scale(1, 1);
    }
    40% {
        transform: scale(1, 0.4); /* Squeeze horizontally */
    }
    100% {
        transform: scale(1, 1); /* Return to original size */
    }
}

//Sticky Nav
.sticky {
    background-color: #ffffff;
    border-bottom: 1px solid #1d213069;
    padding: 14px 0;

    .nav-items {
        color: $black;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
    }

    .nav-logo img {
        height: 28px;
        content: url("../uploads/10897114103101114116104116105/logo.svg");
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
    }

    .w-button-primary {
        padding: 10px 32px;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;

        &:hover {
            background-color: $black !important;

            .w-button-inner {
                color: $white !important;
            }
        }
    }

}